import React, { useState, useEffect } from 'react'
import { Fade } from '@material-ui/core'

import items from './items.js'

import './App.css'

const FULL_ANIMATION = 10000
const SLIDE_IMAGE = FULL_ANIMATION / 2
const FADE_DATE = 500
const FADE_TEXT = 1000
const SLIDE_DURATION = 500

const Image = ({ src }) => {
  return <img className="image" src={src} alt="bilu" />
}

const Text = ({ date, subtitle }) => {
  const [fadeDate, setFadeDate] = useState(false)
  const [fadeSubtitle, setFadeSubtitle] = useState(false)

  useEffect(() => {
    setTimeout(() => setFadeDate(true), FADE_DATE)
    setTimeout(() => setFadeSubtitle(true), FADE_DATE + FADE_TEXT)
  }, [])

  return (
    <div className="text">
      <Fade in={fadeDate}>
        <div className="date">{date}</div>
      </Fade>
      <Fade in={fadeSubtitle}>
        <div className="subtitle">{subtitle}</div>
      </Fade>
    </div>
  )
}

const Item = ({ item }) => {
  const [slideText, setSlideText] = useState(false)
  const [slideImage, setSlideImage] = useState(false)

  const { date, subtitle, src } = item

  useEffect(() => {
    setTimeout(() => {
      setSlideText(true)
    }, SLIDE_DURATION)

    setTimeout(() => {
      setSlideText(false)
    }, SLIDE_IMAGE - SLIDE_DURATION)

    setTimeout(() => {
      setSlideImage(true)
    }, SLIDE_IMAGE)

    setTimeout(() => {
      setSlideImage(false)
    }, FULL_ANIMATION - SLIDE_DURATION)
  }, [item])

  return (
    <>
      <Fade mountOnEnter unmountOnExit in={slideText} direction="left">
        <div>
          <Text date={date} subtitle={subtitle} />
        </div>
      </Fade>
      <Fade mountOnEnter unmountOnExit in={slideImage} direction="left">
        <div>
          <Image src={src} />
        </div>
      </Fade>
    </>
  )
}

const Cabou = ({ onClick }) => (
  <div className="cabou">
    cabou :c
    <div onClick={onClick} className="subtitle denovo">
      de novo?
    </div>
  </div>
)

const App = () => {
  const [index, setIndex] = useState(0)
  const [intvl, setIntvl] = useState()

  useEffect(() => {
    setIntvl(
      setInterval(() => {
        setIndex(prev => prev + 1)
      }, FULL_ANIMATION)
    )
  }, [])

  const item = items[index]
  if (index >= items.length) clearInterval(intvl)

  return (
    <div className="App">
      {index >= items.length ? (
        <Cabou
          onClick={() => {
            setIndex(0)
            setIntvl(
              setInterval(() => {
                setIndex(prev => prev + 1)
              }, FULL_ANIMATION)
            )
          }}
        />
      ) : (
        <Item item={item} />
      )}
    </div>
  )
}

export default App
